import { Component } from 'react'
import Section from "./Section"
import Color from "./Color"
import Headings from "./Headings"
import Paragraph from './Paragraph'
import Menu from './Menu'

class Container extends Component {
    
    constructor(props) {
        super(props)

        this.state = {
            font: 'sans-serif',
            color: [
                'rgba( 255, 0, 0, .3 )',
                'rgba( 255, 0, 0, .8 )',
                'red'
            ]
        }

        this.changeFont = this.changeFont.bind(this)
        this.changeColor = this.changeColor.bind(this)
    }

    changeFont(e) {
        this.setState({
            font: (this.state.font == 'serif' ? 'sans-serif' : 'serif')
        })
    }

    changeColor(e) {
        switch(e.target.value) {
            case 'red':
                this.setState({
                    color: [
                        'rgba( 255, 0, 0, .3 )',
                        'rgba( 255, 0, 0, .8 )',
                        'red'
                    ]
                })
            break;
            case 'blue': 
                this.setState({
                    color: [
                        'rgba( 0, 0, 255, .3 )',
                        'rgba( 0, 0, 255, .8 )',
                        'blue'
                    ]
                })
            break;
            default: 
                this.setState({
                    color: [
                        'rgba( 255, 0, 0, 0, .3 )',
                        'rgba( 255, 0, 0, 0, .8 )',
                        'red'
                    ]
                })
        }
    }

    render() {
        return (
            <>
            <Menu changeFont={this.chagneFont} changeColor={ this.changeColor }/>
            <Section title="01.Typography">
                <Headings font={ this.state.font } />
                <Paragraph font={ this.state.font }/>
            </Section>
            <Section title="02.Color">
                <Color colors={["white", "black-19a", "black-39a", "black-59a", "black-79a", "black-89a", "black-100a"]}/>
                <Color colors={ this.state.color } />
            </Section>
            </>
        )
    }
}

export default Container